.container {
    height: 100vh;
    overflow: hidden;
  }
  .container > .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  
  .container > .grid > .left {
    position: relative;
  }
  
  .container > .grid > .left > .overlay {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.6);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .container > .grid > .left > .overlay > a {
    color: white;
    font-weight: 600;
    font-size: 25px;
    transition: all .5s ease;
    padding:10px;
  }
  
  .container > .grid > .left > .overlay > a:hover{
      box-shadow: 0px 0px 5px gray;
  }
  .container > .grid > .left > .image {
    width: 100%;
    height: 100vh;
  }
  
  .container > .grid > .right {
    /* background-color: red; */
    display: flex;
        justify-content: center;
        align-items: center;
    flex-direction: column;
    gap: 30px;
  }
  
  .container > .grid > .right .heading {
    color: rgb(68, 67, 67);
    font-size: 25px;  
    height: 60px;
    text-align: center;
  }
  
  .container > .grid > .right .heading::after{
    content: "\f015";
    font-family: "Font Awesome 5 Free";
    width: 80px;
    height: 12px;
    color:  #33a137;
    font-size: 25px;
    display: block;
    margin: 0  auto;
    border-bottom: 4px solid #33a137;
  }

  
   
  
  .container > .grid > .right > .form {
    display: flex;
    flex-direction: column;
    width: 500px;
    gap: 20px;
  }
  
  .container > .grid > .right > .form > div {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  .container > .grid > .right > .form > div > input {
    padding: 10px 5px;
    border: none;
    box-shadow: 0px 0px 5px gray;
    outline: none;
    border-radius: 5px;
  }
  
  .container > .grid > .right > .form > div > label > span {
    color: red;
  }
  
  .container > .grid > .right > .form > .submit {
    width: 100px;
    margin-left: auto;
    background-color: #33a137;
    color: white;
    border: none;
    padding: 10px 0px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: .9px;
  }
  