
.contact_us_2 * {
    font-family: Nunito, sans-serif;
  }
 
.blueBG > img{
    width: 100vw;
    height: 300px;
    object-fit: cover;
}

.blueBG::after{
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background: rgba(15,28,47,.6);
    width: 100vw;
    height: 300px;
}

  

  .mainHeading{
    margin-bottom: 40px;
    width: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.heading {
  text-align: center;
  width: 600px;
  height: 60px;
  color: rgb(68, 67, 67);
  
}

.heading h1::after{
  content: "\f015";
  font-family: "Font Awesome 5 Free";
  width: 320px;
  height: 12px;
  color:  #33a137;
  display: block;
  margin: 0  auto;
  font-size: 24px;
  border-bottom: 4px solid #33a137;
}

.inputContainer{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-bottom: 30px;
}

.inputContainer > input {
    padding: 15px 5px;
    border-radius: 5px;
    outline: none;
    border: 1px solid lightgrey;
    color: gray;
}

.inputContainer > input::placeholder{
  color: gray;
}
.inputContainer > textarea{
    border: 1px solid lightgrey;
    padding: 15px 5px;
    outline: none;
    color: gray;
    grid-column: -3/-1;
    resize: none;
    height: 150px;
    border-radius: 5px;
}

.inputContainer > textarea::placeholder{
  color: gray;
}

.heading::after{
    content: "\f015";
    font-family: "Font Awesome 5 Free";
    width: 130px;
    height: 12px;
    color:  #33a137;
    display: block;
    margin: 0  auto;
    border-bottom: 4px solid #33a137;
}

.mainHeading > p{
    text-align: center;
    color: rgb(94, 93, 93);
}

  .contact_us_2 .text-blk {
    margin-top: 0px;
    margin-right: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    line-height: 25px;
  }
  
  .contact_us_2 .responsive-cell-block {
    min-height: 75px;
  }
  
  .contact_us_2 input:focus {
    outline-color: initial;
    outline-style: none;
    outline-width: initial;
  }
  
  .contact_us_2 .container-block {
    min-height: 75px;
    width: 100%;
    padding-top: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    display: block;
    
  }
  
  .contact_us_2 .submit-btn:hover {
    box-shadow: 0px 0px 5px gray;
  }
  
  .contact_us_2 .responsive-container-block {
    min-height: 75px;
    height: fit-content;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-top: 0px;
    margin-right: auto;
    margin-bottom: 50px;
    margin-left: auto;
    padding-top: 0px;
    padding-right: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
  }
  
  .contact_us_2 .responsive-container-block.big-container {
    padding-top: 10px;
    padding-right: 30px;
    padding-bottom: 10px;
    padding-left: 30px;
    position: relative;
    height: auto;
  }
  
  .contact_us_2 .responsive-container-block.container {
    position: static;
    min-height: 75px;
    flex-direction: column;
    z-index: 2;
    max-width: 800px;
    margin-top: 120px;
    margin-right: auto;
    margin-bottom: 50px;
    margin-left: auto;
  }
  
  .contact_us_2 .container-block.form-wrapper {
    background-color: white;
    max-width: 799px;
    text-align: center;
    padding-top: 50px;
    padding-right: 40px;
    padding-bottom: 50px;
    padding-left: 40px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 4px 20px 7px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
    margin-top: 0px;
    margin-right: auto;
    margin-bottom: 0px;
    margin-left: auto;
  }
  
  .contact_us_2 .text-blk.contactus-head {
    font-size: 36px;
    line-height: 50px;
    font-weight: 900;
  }
  
  .contact_us_2 .text-blk.contactus-subhead {
    color: #9c9c9c;
    margin-top: 0px;
    margin-right: 0px;
    margin-bottom: 50px;
    margin-left: 0px;
  }
  
  .contact_us_2 .responsive-cell-block.wk-desk-6.wk-ipadp-6.wk-tab-12.wk-mobile-12 {
    margin-top: 0px;
    margin-right: 0px;
    margin-bottom: 26px;
    margin-left: 0px;
    min-height: 50px;
  }
  
  .contact_us_2 .input {
    width: 96%;
    height: 50px;
    padding-top: 1px;
    padding-right: 15px;
    padding-bottom: 1px;
    padding-left: 15px;
    border-top-width: 2px;
    border-right-width: 2px;
    border-bottom-width: 2px;
    border-left-width: 2px;
    border-top-style: solid;
    border-right-style: solid;
    border-bottom-style: solid;
    border-left-style: solid;
